@mixin flex-space-between {
  display: flex;
  justify-content: space-between;
}

@mixin description {
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
}

@mixin descriptionTitle {
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}
